import './sass/main.scss';

const refs = {
    bodyContainer: document.querySelector('body'),
    checkbox: document.getElementById('theme-switch-toggle'),
};

console.log(refs.checkbox);
console.log(refs.bodyContainer);



const Theme = {
    LIGHT: 'light-theme',
    DARK: 'dark-theme',
};


document.addEventListener('DOMContentLoaded', defaultLocalSt);
const localStorageTheme = localStorage.getItem('themeNow');
console.log(localStorageTheme);

refs.checkbox.addEventListener('click', changeTheme);

function defaultLocalSt() {
    console.log("Проверяем Локал")
    
    if (!localStorageTheme) {
        localStorage.setItem('themeNow', Theme.LIGHT),
        refs.bodyContainer.classList.add(Theme.LIGHT);
        console.log("Дефолт Лайт");
    } else {
        refs.bodyContainer.classList.add(Theme.DARK);
        refs.checkbox.checked = true;
    }
}

