function changeTheme(event) {
    console.log("меняем тему")
    let themeNow = localStorage.getItem('themeNow');

    if (themeNow === 'light-theme') {
        console.log('светлая тема');

        localStorage.setItem('themeNow', Theme.DARK);
        refs.bodyContainer.classList.replace(Theme.LIGHT, Theme.DARK);
        console.log("меняем на", Theme.DARK);
    } else {
        console.log("темная тема");
        localStorage.setItem('themeNow', Theme.LIGHT);
        refs.bodyContainer.classList.replace(Theme.DARK, Theme.LIGHT);

        console.log("меняем на", Theme.LIGHT);
    }
}



